.property-card {
    width: 340px;
    height: 340px;
    position: relative; 
    border-radius: 10px; 
    overflow: hidden; 
  
    img {
    width: 100%;
    height: 100%;
    object-fit: cover; 
    }
  
    h3 {
    position: absolute;
    bottom: 0; 
    left: 0; 
    max-width: 300px;
    width: 100%; 
    margin-left: 20px;
    box-sizing: border-box;
    color: #FFF;
    font-family: 'Montserrat';
    font-size: 18px;
    font-weight: 500;
    }
    
}  

section p {
    color: #FF6060;
}